
.importante {
    max-width: 38rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.importante p {
    font-size: 16px;

}



.titulo {
    color: white;
    background-color: hsl(248, 32%, 49%);
    border-radius: 7px;
    padding: 5px;
    font-size: 16px;
    margin-bottom: 15px;

}

.titulo strong {
    font-size: 17px;
    font-family: 'Poppins', sans-serif;
}

.General {

    color: white;
    display: flex;
    flex-direction: column;
    gap: 50px;
    text-align: center;




}

.formulario {
    max-width: 38rem;



}

.datos {
    background-color: white;
    color: black;
    padding: 20px;
    border-radius: 8px;




}

.datos input {
    width: 100%;
    border-radius: 8px;
    padding: 15px 0px 15px 0px;
    border: none;
    border: 1px solid rgba(90, 85, 81, 0.95);
    display: block;



}

.cuadros {
    text-align: left;
    margin-top: 10px;

}

.contenedor-texto {
    position: relative;
    color: red($color: #000000);
}

.icon-error {
    position: absolute;
    right: 15px;
    top: 10px;
    display: none;

}

.mensaje-error {
    text-align: right;
    color: brown;

}

.boton {
    background-color: hsl(154, 59%, 51%);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 20px 30px;
    width: 100%;
    color: white;
    font-size: 16px;
    margin-top: 10px;




}

#Terminos1 {
    color: hsl(246, 25%, 77%);
    font-size: 16px;

}

.terminos2 {
    color: hsl(0, 100%, 74%);
    text-decoration: none;
}

input {
    border-color: hsl(249, 10%, 26%);
}



@media(min-width:1100px) {


    .General {
        flex-direction: row;

    }

    .importante {
        text-align: left;
    }
}













